.user-info__edit-button {
    width: 24px;
    height: 24px;
    margin-left: 19px;
    border: 1px solid white;
    background: url("../../../images/edit-button.svg") center no-repeat;
}

@media screen and (max-width: 980px) {
    .user-info__edit-button {
        margin-left: 10px;
        width: 18px;
        height: 18px;
    }
}

.user-info__edit-button:hover {
    background: url("../../../images/edit-button-hover.svg") center no-repeat;
}
