.user-info__about {
    margin: 5px 0 0 0;
    font-size: 18px;
    line-height: 22px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 980px) {
    .user-info__about {
        margin-bottom: 28px;
        margin-top: 0;
        font-size: 14px;
        line-height: 17px;
    }
}
